import axios from "axios";

//ajax

axios.interceptors.response.use(null, error => {

  const exceptedError =

    error.response && error.response.status >= 400 && error.response <= 500;

  if (!exceptedError) {
        console.log(error);
  }

  Promise.reject(error);

});

 

export default {

  get: axios.get,

  post: axios.post,

  put: axios.put,

  delete: axios.delete

};