import React from 'react';

export default class Button extends React.Component{

    _renderCorrectButton(){
        const { toggle , buttonSettings } = this.props;
        const {type, buttonIn, buttonOut } = buttonSettings;
        switch(type){
            case 'toggle':
            return(
                <span className={!toggle ? buttonIn: buttonOut}>
                </span>      
            ); 
        }
    }

    render(){
        const { buttonSettings } = this.props;
        const {type, buttonIn, buttonOut, classList } = buttonSettings;
        return (
            <button className={`button ${classList}`} onClick={this.props.clickTrigger}>
                { this._renderCorrectButton() }
            </button>     
        );   
    }
          
    
}