// Set names for actions to use throughout application
export const TOGGLE_SIDE_NAVIGATION  =   "TOGGLE_SIDE_NAVIGATION";
export const USER_AUTHENTICATED  =   "USER_AUTHENTICATED";
export const toggleSideNavigation = ()=> ({
        type    :   TOGGLE_SIDE_NAVIGATION,
});

export const toggleUserAuthenticated = ()=> ({
        type    :   USER_AUTHENTICATED,
});