import React from 'react';
import { connect } from 'react-redux';
import { findCurrentPage } from '../../../store/actions/pages';

class Company extends React.Component{
    
    componentDidMount()
    {
        this.props.onFindCurrentPage();        
    }

    // return JSX
    render(){
        if(this.props.currentPage)
            console.log('company', this.props.currentPage);
        const { pageTitle } = this.props.currentPage
            return (
                <div>
                    <h1>{pageTitle}</h1>
                </div>
            );
        
    }
}

const mapDispatchToProps = (__dispatch) => {
    return {
        onFindCurrentPage   :   () => __dispatch(findCurrentPage('company'))
    }
}

// Connect component to store
const mapStateToProps = (state)=>{
    return {
        pages       :   state.json.pages.pages,
        currentPage :   state.json.pages.currentPage
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Company);