import React from 'react';
import { connect } from 'react-redux';
import { findCurrentPage } from '../../store/actions/pages';
import http from '../../services/Http';
class Dashboard extends React.Component{
    state = {
        siteNames: []
      }
     componentDidMount()
    {
        this.props.onFindCurrentPage();    
    }

    // return JSX
    render(){
        console.log(this.state.siteNames);
        if(this.props.currentPage){
            const { greetingTop, greetingSub, lastLoggedIn } = this.props.currentPage;
            return (
                <section className="grid__container u-padding-top-from-nav">
                    <h1>{ greetingTop }</h1>
                    <h2>{ greetingSub }</h2>
                    <p>{ lastLoggedIn }</p>
                </section>
            );
        }else{
            return false
        }
        
    }
}

const mapDispatchToProps = (__dispatch) => {
    return {
        onFindCurrentPage   :   () => __dispatch(findCurrentPage('dashboard'))
    }
}

// Connect component to store
const mapStateToProps = (state)=>{
    return {
        pages       :   state.json.pages.pages,
        currentPage :   state.json.pages.currentPage
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);