import React from 'react';
import { connect } from 'react-redux';
import { toggleSideNavigation } from '../../store/actions/applicationState';

// Import web components
import Button from '../web-components/Button';
import Dropdown from '../web-components/dropdown/DropDown';

class TopNav extends React.Component{
    constructor(props){
        super(props);
        this._navIconClicked = this._navIconClicked.bind(this);
    }

    _navIconClicked(){
        this.props.toggleNavigation();
    }

  // return JSX
  render(){
      //Destructure props store topNav object
      const { logo, menu, button } = this.props.topNav;
      // Destructure props from parent 
      const { sideNavDisplay} = this.props;
        return (
            <header className="header">
                <Button 
                        toggle={sideNavDisplay}
                        clickTrigger={this._navIconClicked}
                        buttonSettings={button}/>
                <div className="logo">
                    <img className="logo__img" alt="" src="img/sprite.svg#logo"/> 
                </div>
                <Dropdown settings={menu}/>
            </header>
        );
    }
}



const mapDispatchToProps = (__dispatch) => {
    return {
        toggleNavigation   :   () => __dispatch(toggleSideNavigation())
    }
}

// Connect component to store
const mapStateToProps = (state)=>{
    return {
        topNav: state.json.topNav,
        sideNavDisplay : state.applicationNavigation.appSideNav
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(TopNav);
