// Application Pages reducer
import * as pageActions from '../../actions/pages';

const _pagesDefaultState = {
    pages : [
        { 
            page: 'dashboard',
            pageTitle: 'dashboard',
            greetingTop: "hello there busy bee!",
            greetingSub: "welcome to grypp",
            lastLoggedIn: "You last logged in 21:15 2nd April",
            settings:""
        },
        { 
            page: 'connections',
            pageTitle: 'connections'
        },
        { 
            page: 'company',
            pageTitle: 'company not mvp'
        },
        { 
            page: 'users_all',
            pageTitle: 'users all not MVP'
        },
        { 
            page: 'users_new',
            pageTitle: 'users new not MVP'
        },
        { 
            page: 'users_upload',
            pageTitle: 'users upload not MVP'
        },
        { 
            page: 'documents_add',
            pageTitle: 'Add a document'
        },
        { 
            page: 'documents_edit',
            pageTitle: 'Edit a document'
        },
        { 
            page: 'tags',
            pageTitle: 'Create Tags not MVP'
        },
        { 
            page: 'metrics_summary',
            pageTitle: 'Metrics summary'
        },
        { 
            page: 'mode_confirm_callContainer',
            pageTitle: 'Call Container'
        },
        
        
    ],
    currentPage : null
};

export default (__state = _pagesDefaultState, __action) =>{
    const __updatedState = {...__state};
    switch(__action.type){
        case pageActions.FIND_CURRENT_PAGE:
            // reset current page as can have different values
            __updatedState.currentPage = null;
            let __page = null;
            if(__action.name)
            {
                __page = __state.pages.find((__pageToFind)=> __pageToFind.page === __action.name);
            }
            __updatedState.currentPage = __page;
            break;
        default:
        // do nothing;
    }
    return __updatedState
};
