/**
 * Renders the Applications MAIN Navigation
 * Props Required from parent are :- 
 * showSideNav : boolean - used to display navigation
 * Props connected to redux store are sideBar - default object found in sidebar navigation reducer
 */
import React from 'react';
import { connect } from 'react-redux';


import Dropdown from '../web-components/dropdown/DropDown';

class UiNav extends React.Component{
    // return JSX
    render(){
        const {sideNavDisplay} = this.props;
        return (
            <nav className={"ui-nav " + (sideNavDisplay ? 'show' : 'hidden')}>
                <Dropdown settings={this.props.sideBar.mainMenu}/> 
            </nav>
        );
    }
}
// Connect component to store
const mapStateToProps = (state)=>{
    return {
        sideBar: state.json.sidebar,
        sideNavDisplay : state.applicationNavigation.appSideNav
    }
}

export default connect(mapStateToProps)(UiNav);