import React from 'react';
import { connect } from 'react-redux';
// import react router more info at 
//https://reacttraining.com/react-router/native/guides/quick-start
// use switch as it renders routes exclusively
import{ BrowserRouter, Route, Switch, Link, NavLink } from 'react-router-dom';

// Import application pages
import Login from '../components/pages/Login';
import Dashboard from '../components/pages/Dashboard';
import NotFoundPage from '../components/pages/NotFoundPage';
import Tags from '../components/pages/Tags';

// System Pages 
import Company from '../components/pages/system/Company';
import Connections from '../components/pages/system/Connections';
import UsersAll from '../components/pages/system/users/UsersAll';
import UsersNew from '../components/pages/system/users/UsersNew';
import UsersUpload from '../components/pages/system/users/UsersUpload';

// Partials
import TopNav from '../components/partials/TopNav';
import SideNav from '../components/partials/SideNav';

// Plugins
// Documents
import DocumentsAdd from '../components/plugins/documents/DocumentsAdd';
import DocumentsEdit from '../components/plugins/documents/DocumentsEdit';
import DocumentsEditSingle from '../components/plugins/documents/DocumentsEditSingle';

// Metrics
import MetricsSummary from '../components/metrics/MetricsSummary';

// Modes
// Confirm
class UiRouter extends React.Component{
    
    render(){
        const { userAuthenticated } = this.props;
        return <BrowserRouter>
        {/* <TopNav />
        <SideNav /> */}
        <TopNav />
        <SideNav />
        <Switch>
            <Route path="/" component={Dashboard} exact={true} />
            <Route path="/system/company" component={Company}/>
            <Route path="/system/connections" component={Connections}/>
            <Route path="/system/users/all" component={UsersAll}/>
            <Route path="/login" component={Login}/>
            <Route path="/system/users/new" component={UsersNew}/>
            <Route path="/system/users/upload" component={UsersUpload}/>
            <Route path="/plugins/documents/add" component={DocumentsAdd}/>
            <Route path="/plugins/documents/edit" component={DocumentsEdit}/>
            <Route path="/plugins/documents/edit/:id" component={DocumentsEditSingle}/>
            <Route path="/tags" component={Tags}/>
            <Route path="/metrics/summary" component={MetricsSummary}/>
            <Route component={NotFoundPage}/>
        </Switch>  
        </BrowserRouter>
    }   
}

// Connect component to store
const mapStateToProps = (state)=>{
    return {
        userAuthenticated : state.applicationNavigation.userAuthenticated
    }
}

export default connect(mapStateToProps)(UiRouter);
