/**
 * OBJECT STRUCTURE TO BUILD DROPDOWN
 * (property): [
 * {
        type: string dropdown/navLink;
        route: string;
        activeClass: string;
        label: string;
        icon: {
            display: boolean;
            url: string;
        };
        children: [
            {
                type: string dropdown/navLink;
                route: string;
                activeClass: string;
                label: string;
                icon: {
                    display: boolean;
                    url: string;
                };
                children: [];
                }   
        ];
    }
 *
 */

import React from 'react';
import  Items  from './Items';

export default class Dropdown extends React.Component{
    render(){
        const {settings} = this.props;
        if(settings && settings.links.length > 0){
            return(
                    <ul className={`dropdown  ${settings.mainClass.join(' ')} `}>
                        <Items settings={settings.links}
                                ddType={settings.type}/>
                    </ul>     
            );
        }else{
            return(
                 <p>Something is wrong inside Dropdown Js Settings.</p>
            )
        }
        
    }
}