import * as appNavigation from '../actions/applicationState';
const appDefaultState = {
    appSideNav : false,
    userAuthenticated : false
}

export default (__state = appDefaultState, __action)=>{
    // assign var to state
    const __updatedState = {...__state};
    switch(__action.type){
        case appNavigation.TOGGLE_SIDE_NAVIGATION:
        __updatedState.appSideNav = !__updatedState.appSideNav
        case appNavigation.USER_AUTHENTICATED:
        __updatedState.userAuthenticated = !__updatedState.userAuthenticated
        default:
        //do nothing 
    }
    return __updatedState;
}